        <span class="hljs-tag">&lt;<span class="hljs-name">vc-graphics-point</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"point3"</span> <span class="hljs-attr">:color</span>=<span class="hljs-string">"color3"</span> <span class="hljs-attr">:pixel-size</span>=<span class="hljs-string">"32"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">vc-graphics-point</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">vc-entity</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">vc-viewer</span>&gt;</span>
